#about {
    background-color: rgb(95, 95, 95);
    padding-top: 4rem;
    padding-bottom: 12rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    color: rgb(212, 207, 207);
}


#about p {
    font-size: 14pt;
}

.about-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    object-fit: contain;
}

.socials-container {
    gap: 30px;
    padding: 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.socials-container .social-link {
    margin: 50%;
}

.info-container {
    display: flex;
    flex-direction: column;
    justify-content: left;
    text-align: left;
    padding-top: 50px;
    width: 100%;
    height: 100%;
}

.links-container {
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: left;
    gap: 30px;
}

.line {
    display: inline-block;
    width: 500px;
    height: 1px;
    background-color: rgb(212, 207, 207);
    vertical-align: middle;
    margin-left: 20px;
}

.row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: 400px;
    width: 100%;
    float: left;
    padding: 10px;
}

.socials {
    flex: 10%;
}

.image {
    flex: 20%;
}

.info {
    flex: 50%;
}

.links {
    flex: 20%;
}

.profile {
    width:90%;
    height:90%;
    object-fit: cover;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.subtitle {
    text-align: left;
}



@media screen and (max-width: 960px) {
    .column {
      width: 100%;
    }
  }
ul {
    list-style-type: none;
}

.btn--social {
    font-size: 20px;
    line-height: 40px;
    display: block;
    width: 40px;
    height: 40px;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
    color: white;
    border-radius: 100%;
    outline: none;
    background-color: #212529;
    text-align: center;
}
.btn--social:hover {
    background: #fff;
    font-size: 30px;
    color: #242424;
    transition: all 0.3s ease-out;
}