
.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    transition: all 0.3s ease-out;
}

.btn--social {
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}
.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover {
    background: #fff;
    color: #242424;
    transition: all 0.3s ease-out;
}