#footer {
    background-color: rgb(212, 207, 207);
    padding-top: 1rem;
    padding-bottom: 1rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    color: rgb(95, 95, 95);
}

#footer h1 {
    text-align: center;
}

.info {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content:center;
    max-width: 70%;
    width: 80%;
    margin: 0 auto;
}

.email,.phone {
    display: flex;
    flex-flow: column;
    justify-content: center;
    gap: 5px;
}

.links {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    gap: 20px;
    margin-right: 130px;
}

.email-head,.phone-head {
    font-weight: bold;
    text-align: center;
}

